@keyframes swing {
  15% {
    transform: translateX(9px);
  }

  30% {
    transform: translateX(-9px);
  }

  40% {
    transform: translateX(6px);
  }

  50% {
    transform: translateX(-6px);
  }

  65% {
    transform: translateX(3px);
  }

  100% {
    transform: translateX(0);
  }
}

.swing:hover {
  animation: swing .6s;
}

.grow {
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
}

.grow:hover {
  transform: scale(1.2);
}

